import RootService from '../services'
import RootStore from './index'
import AbstractResourceStore from './AbstractResourceStore'
import Person from '../domain/Person'
import PersonService from '../services/PersonService'
import {action, computed, makeObservable, observable} from 'mobx'
import Organization from '../domain/Organization'

export default class PersonStore extends AbstractResourceStore<Person> {
  private readonly personService: PersonService
  private readonly rootStore: RootStore
  @observable private selectedPersonId: string

  public constructor(rootService: RootService, rootStore: RootStore) {
    super(rootService.personService, rootStore.accountStore)
    this.personService = rootService.personService
    this.rootStore = rootStore
    makeObservable(this)
  }

  public async loadUserRelated() {
    await this.setManyEventually(this.personService.getAllRelatedToUser())
  }

  public async loadByOrganization(organizationId: string) {
    await this.setManyEventually(this.personService.getAllByOrganization(organizationId))
  }

  protected prepareForStoring(item: Person): Person {
    const extendedItem = new StoredPerson(this.rootStore).fromObject(item)

    return super.prepareForStoring(extendedItem)
  }

  @computed
  public get currentUserPersons(): Person[] {
    const authenticatedUserId = this.accountStore.authenticatedUser?.id

    if (!authenticatedUserId) {
      return []
    }

    return this.items.filter(p => p.userId === authenticatedUserId)
  }

  @computed
  public get currentUserNonActivePersons(): Person[] {
    const persons = this.currentUserPersons

    return persons.filter(p => p.id !== this.currentPerson.id)
  }

  @computed
  public get currentPerson(): Person | null {
    const selectedPerson = this.selectedPersonId && this.currentUserPersons.find(p => p.id === this.selectedPersonId)

    return selectedPerson || this.currentUserPersons[0] || null
  }

  @action
  public selectPerson(id: string): void {
    this.selectedPersonId = id
  }
}

class StoredPerson extends Person {
  private readonly rootStore: RootStore

  constructor(rootStore: RootStore) {
    super()
    this.rootStore = rootStore
  }

  @computed
  get organization(): Organization {
    if (this.organizationId && !this._organization) {
      return this.rootStore.organizationStore.getByIdOrLoad(this.organizationId)
    }

    return this._organization
  }
}