import React, {useEffect} from 'react'
import AuthenticationGuard from '../components/AuthenticationGuard'
import {Redirect, useHistory} from 'react-router-dom'
import useStore from '../hooks/useStore'
import useServices from '../hooks/useServices'

const Start: React.FC = () => {
  const {accountStore} = useStore()
  const {accountService} = useServices()
  const history = useHistory()

  useEffect(() => {
    const email = prompt('Please provide your email')

    if (!email) {
      history.replace('/')
      return
    }

    accountStore.login(email, '1')
        .catch(() => {
          console.info('Login failed, creating account...')

          return true
        })
        .then(() => {
          return accountService.register(email, '')
        })
        .then(() => {
          console.info('Account created, logging in')

          return accountStore.login(email, '')
        })
        .then(() => {
          history.replace('/')
        })
        .catch(err => {
          console.error(err)
          alert('Failure')
        })
  })

  return null
}

const GuardedStart: React.FC = () => {
  return (
      <AuthenticationGuard forbiddenAction={<Start />}>
        <Redirect to="/app" />
      </AuthenticationGuard>
  )
}

export default GuardedStart