import Organization from './Organization'
import User from './User'
import AbstractDomain from './AbstractDomain'
import {JsonProperty} from 'jackson-js'
import Resource from '../decorators/Resource'

export enum Role {
    ADMIN,
    MANAGER,
    USER,
}

@Resource.Name('persons')
export default class Person extends AbstractDomain<Person>{
    @JsonProperty() public name: string
    @JsonProperty() public position: string
    @JsonProperty() public userId?: string | null
    @JsonProperty() public user?: User
    @JsonProperty() public capacity?: number | null
    @JsonProperty() public skillIds: string[]
    @JsonProperty() public organizationId: string
    @JsonProperty() public role: Role
    @JsonProperty() public image?: string
    protected _organization?: Organization

    public get subTitle(): string {
        const pieces = [this.position, this.organization?.name].filter(Boolean)

        return pieces.join(' at ')
    }

    public get organization(): Organization {
        return this._organization
    }

    public set organization(value: Organization) {
        this._organization = value
    }
}
