import {ObjectMapper} from 'jackson-js'
import {ClassType} from 'jackson-js/dist/@types'

export default class LocalStorageService<T> {
  private static KEY_PREFIX = 'LSS/'
  private readonly mapper = new ObjectMapper()

  public setItem(key: string, value: T): void {
    console.info(`LSS: Storing item "${key}"`)
    localStorage.setItem(this.getFullKey(key), this.mapper.stringify(value))
  }

  public getItem(key: string, type?: ClassType<T>): T {
    console.info(`LSS: Retrieving item "${key}"`)
    const result = localStorage.getItem(this.getFullKey(key))

    if (!result) {
      return null
    }

    return this.mapper.parse(result, type ? {mainCreator: () => [type]} : {})
  }

  public removeItem(key: string): void {
    console.info(`LSS: Removing item item "${key}"`)
    localStorage.removeItem(this.getFullKey(key))
  }

  private getFullKey(key: string): string {
    return LocalStorageService.KEY_PREFIX + key
  }
}