import React from 'react'
import {useForm, UseFormMethods, FormProvider} from 'react-hook-form'

export interface ProjectListProps {
  onSubmit: (values: object) => void | Promise<void>
  children: React.ReactNode | ((formContext: UseFormMethods) => any)
}

const Form: React.FC<ProjectListProps> = ({onSubmit, children}) => {
  const formContext = useForm()

  return (
      <FormProvider {...formContext}>
        <form onSubmit={formContext.handleSubmit(onSubmit)}>
          {typeof children === 'function' ? (children as any)(formContext) : children}
        </form>
      </FormProvider>
  )
}

export default Form