import React from 'react'
import {Menu as ChakraMenu, MenuButton, MenuList, MenuItem} from '@chakra-ui/react'

interface UserMenuProps {
  handle: React.ReactNode
}

const Menu: React.FC<UserMenuProps> = ({handle, children}) => {
  return (
      <ChakraMenu placement="bottom-end" isLazy offset={[-7, 0]}>
        <MenuButton p={4}>
          {handle}
        </MenuButton>
        <MenuList>
          {React.Children.map(children, (child, i) => (
              <MenuItem justifyContent="flex-end" key={i}>
                {child}
              </MenuItem>
          ))}
        </MenuList>
      </ChakraMenu>
  )
}

export default Menu