import React, {useEffect} from 'react'
import {observer} from 'mobx-react'
import {Redirect} from 'react-router-dom'
import useStore from '../hooks/useStore'

interface AuthenticationGuardProps {
  forbiddenAction?: any
}

const AuthenticationGuard: React.FC<AuthenticationGuardProps> = ({children, forbiddenAction}) => {
  const {accountStore} = useStore()
  const {authenticated} = accountStore

  useEffect(() => {
    if (authenticated === null) {
      accountStore.checkAuthentication()
    }
  }, [authenticated, accountStore])

  if (authenticated === null) {
    return null
  }

  if (!authenticated) {
    return forbiddenAction
  }

  return <>{children}</>
}

AuthenticationGuard.defaultProps = {
  forbiddenAction: <Redirect to="/home" />
}

export default observer(AuthenticationGuard)