import RootService from '../services'
import RootStore from './index'
import AbstractResourceStore from './AbstractResourceStore'
import Project from '../domain/Project'
import ProjectService from '../services/ProjectService'
import {computed} from 'mobx'
import Organization from '../domain/Organization'

export default class ProjectStore extends AbstractResourceStore<Project> {
  private readonly projectService: ProjectService
  private readonly rootStore: RootStore

  public constructor(rootService: RootService, rootStore: RootStore) {
    super(rootService.projectService, rootStore.accountStore)
    this.projectService = rootService.projectService
    this.rootStore = rootStore
  }

  protected prepareForStoring(item: Project): Project {
    return super.prepareForStoring(new StoredProject(this.rootStore).fromObject(item))
  }

  public async loadByOrganization(organizationId: string) {
    await this.setManyEventually(this.projectService.getAllByOrganization(organizationId))
  }

  public async loadByActiveOrganization() {
    const organizationId = this.rootStore.personStore.currentPerson?.organizationId

    if (!organizationId) {
      throw new Error('No organization selected')
    }

    return this.loadByOrganization(organizationId)
  }

  @computed
  public get currentOrganizationProjects() {
    const organizationId = this.rootStore.organizationStore.currentOrganizationId

    if (!organizationId) {
      return []
    }

    return this.items.filter(project => project.organizationId === organizationId)
  }

  @computed
  public get upcomingProjects() {
    return this.currentOrganizationProjects.filter(project => !project.hasStarted())
  }

  @computed
  public get ongoingProjects() {
    return this.currentOrganizationProjects.filter(project => project.isOngoing())
  }

  @computed
  public get endedProjects() {
    return this.currentOrganizationProjects.filter(project => project.hasStarted() && project.hasEnded())
  }
}

class StoredProject extends Project {
  private readonly rootStore: RootStore

  constructor(rootStore: RootStore) {
    super()
    this.rootStore = rootStore
  }

  @computed
  get organization(): Organization {
    if (this.organizationId && !this._organization) {
      return this.rootStore.organizationStore.getByIdOrLoad(this.organizationId)
    }

    return this._organization
  }
}