import React, {useEffect} from 'react'
import useStore from '../hooks/useStore'
import Layout from '../ui/Layout'
import {Route as NavRoute} from '../routes'
import NavItemGroup from '../ui/NavItemGroup'
import NavItem from '../ui/NavItem'
import {Route, Switch, Link} from 'react-router-dom'
import {observer} from 'mobx-react'
import Menu from '../ui/Menu'
import User from '../ui/User'

interface ConnectedLayoutProps {
  routes: NavRoute[]
}

const ConnectedLayout: React.FC<ConnectedLayoutProps> = ({routes}) => {
  const {personStore} = useStore()
  const {currentUserNonActivePersons, currentPerson} = personStore

  useEffect(() => {
    personStore.loadUserRelated()
  }, [])

  return (
      <Layout
          navigation={(
              <div>
                {routes.map(route => (
                    <NavItemGroup key={route.path} title={route.title}>
                      {route.children?.map(subRoute => (
                          <NavItem
                              key={subRoute.path}
                              to={route.path + subRoute.path}
                              title={subRoute.title}
                              icon={subRoute.icon || route.icon}
                          />
                      ))}
                    </NavItemGroup>
                ))}
              </div>
          )}
          topRight={(
              <Menu handle={<User
                  name={currentPerson.name}
                  subTitle={currentPerson.subTitle}
                  image={currentPerson.image}
              />}>
                {currentUserNonActivePersons.map(({id, name, subTitle, image}) => (
                    <User
                        key={id}
                        name={name}
                        subTitle={subTitle}
                        image={image}
                        onClick={() => personStore.selectPerson(id)}
                    />
                ))}
                <Link to="/logout">Sign out</Link>
              </Menu>
          )}
      >
        <Switch>
          {routes.map(route => (
              <React.Fragment key={route.path}>
                {route.children?.map(subRoute => (
                    <Route
                        key={subRoute.path}
                        path={route.path + subRoute.path}
                        component={subRoute.component}
                    />
                ))}
              </React.Fragment>
          ))}
        </Switch>
      </Layout>
  )
}

export default observer(ConnectedLayout)