import { v4 as uuid } from 'uuid'
import LocalStorageService from './LocalStorageService'
import {ResourceId} from '../types'
import RestfulService from './RestfulService'
import AbstractDomain from '../domain/AbstractDomain'
import {ClassType} from 'jackson-js/dist/@types'
import Resource from '../decorators/Resource'

export default class AbstractRestfulLocalStorageService<T extends AbstractDomain<T>> implements RestfulService<T> {
  private readonly domain: ClassType<T>
  private readonly localStorageService: LocalStorageService<T[]>

  public constructor(domain: ClassType<T>, localStorageService: LocalStorageService<T[]>) {
    this.domain = domain
    this.localStorageService = localStorageService
  }

  private get resource(): string {
    return Resource.getName(this.domain)
  }

  public async getMany(params: object = {}): Promise<T[]> {
    let results = this.localStorageService.getItem(this.resource)

    if (!results) {
      return []
    }

    for (const filter in params) {
      // @ts-ignore
      results = results.filter(r => r[filter] === params[filter])
    }

    return results
  }

  public async getOne(id: ResourceId, params: object = {}): Promise<T> {
    const [result] = await this.getMany({ id })

    if (!result) {
      throw new Error(`Item ${id} not found`)
    }

    return result
  }

  public async update(id: ResourceId, item: Partial<T>): Promise<T> {
    const items = this.localStorageService.getItem(this.resource)

    const updatedItem = items?.find(i => i.id === id)

    if (!updatedItem) {
      throw new Error(`Item ${id} not found`)
    }

    for (const key in item) {
      if (!item.hasOwnProperty(key)) {
        continue
      }
      updatedItem[key] = item[key]!
    }

    this.localStorageService.setItem(this.resource, items)

    return updatedItem
  }

  public async create(item: T): Promise<T> {
    const items = this.localStorageService.getItem(this.resource)

    item.id = uuid()

    this.localStorageService.setItem(this.resource, [
        ...items,
        item
    ])

    return item
  }

  public async delete(id: ResourceId): Promise<void> {
    const items = this.localStorageService.getItem(this.resource)

    this.localStorageService.setItem(this.resource, items?.filter(i => i.id !== id))
  }
}