import React from 'react'
import Organization from '../domain/Organization'
import {Input, VStack, Fade, ScaleFade, Slide, Collapse} from '@chakra-ui/react'
import Logo from '../ui/Logo'
import FormSubmitButton from './FormSubmitButton'
import Form from './Form'
import FormField from './FormField'

export interface OrganizationFormProps {
  onSubmit: (data: any) => void
}

const OrganizationForm: React.FC<OrganizationFormProps> = ({onSubmit}) => {
  return (
      <Form onSubmit={onSubmit}>
        {({watch}) => {
          const organizationName = watch('organization.name')

          return (
              <VStack spacing={8} mt={4}>
                <div style={{width: 150}}>
                  <Logo/>
                </div>
                <FormField name="organization.name" required>
                  <Input
                      size="lg"
                      variant="flushed"
                      placeholder="Organization name"
                      autoFocus
                  />
                </FormField>

                {organizationName && (
                    <ScaleFade in style={{width: '100%'}}>
                      <FormField name="person.name" required>
                        <Input
                            name="person.name"
                            variant="flushed"
                            placeholder="Your name"
                        />
                      </FormField>
                    </ScaleFade>
                )}

                {watch('person.name') && (
                    <ScaleFade in style={{width: '100%'}}>
                      <FormField name="person.position">
                        <Input
                            variant="flushed"
                            placeholder={'Your position in ' + organizationName}
                        />
                      </FormField>
                    </ScaleFade>
                )}

                <FormSubmitButton>
                  Create
                </FormSubmitButton>
              </VStack>
          )
        }}
      </Form>
  )
}

export default OrganizationForm