import AbstractRestfulLocalStorageService from './AbstractRestfulLocalStorageService'
import LocalStorageService from './LocalStorageService'
import ProjectSkill from '../domain/ProjectSkill'

export default class ProjectSkillService extends AbstractRestfulLocalStorageService<ProjectSkill> {
  public constructor(localStorageService: LocalStorageService<ProjectSkill[]>) {
    super(ProjectSkill, localStorageService)
  }

  public getAllByProject(projectId: string): Promise<ProjectSkill[]> {
    return this.getMany({ projectId })
  }
}