import RootService from '../services'
import RootStore from './index'
import AbstractResourceStore from './AbstractResourceStore'
import ProjectSkill from '../domain/ProjectSkill'
import ProjectSkillService from '../services/ProjectSkillService'
import {computed} from 'mobx'
import Project from '../domain/Project'
import Skill from '../domain/Skill'

export default class ProjectSkillStore extends AbstractResourceStore<ProjectSkill> {
  private readonly projectSkillService: ProjectSkillService
  private readonly rootStore: RootStore

  public constructor(rootService: RootService, rootStore: RootStore) {
    super(rootService.projectSkillService, rootStore.accountStore)
    this.projectSkillService = rootService.projectSkillService
    this.rootStore = rootStore
  }

  public async loadByProject(projectId: string) {
    await this.setManyEventually(this.projectSkillService.getAllByProject(projectId))
  }

  protected prepareForStoring(item: ProjectSkill): ProjectSkill {
    return super.prepareForStoring(new StoredProjectSkill(this.rootStore).fromObject(item))
  }

  public getItemsByProject(projectId: string): ProjectSkill[] {
    return this.items.filter(i => i.projectId === projectId)
  }
}

class StoredProjectSkill extends ProjectSkill {
  private readonly rootStore: RootStore

  constructor(rootStore: RootStore) {
    super()
    this.rootStore = rootStore
  }

  @computed
  get project(): Project {
    if (this.projectId && !this._project) {
      return this.rootStore.projectStore.getByIdOrLoad(this.projectId)
    }

    return this._project
  }


  @computed
  get skill(): Skill {
    if (this.skillId && !this._skill) {
      return this.rootStore.skillStore.getByIdOrLoad(this.skillId)
    }

    return this._skill
  }
}