import AbstractDomain from './AbstractDomain'
import Organization from './Organization'
import {JsonProperty} from 'jackson-js'
import Resource from '../decorators/Resource'

export enum ProjectState {
    UPCOMING,
    ONGOING,
    ENDED,
}

@Resource.Name('projects')
export default class Project extends AbstractDomain<Project> {
    @JsonProperty() public name: string
    @JsonProperty() public dateStart?: Date
    @JsonProperty() public dateEnd?: Date
    @JsonProperty() public organizationId: string
    protected _organization?: Organization

    @JsonProperty()
    get organization(): Organization {
        return this._organization
    }

    set organization(value: Organization) {
        this._organization = value
    }

    public hasStarted(date: Date = new Date()): boolean {
        return !this.dateStart || this.dateStart < date
    }

    public hasEnded(date: Date = new Date()): boolean {
        return this.dateEnd && this.dateEnd < date
    }

    public isOngoing(): boolean {
        return this.hasStarted() && !this.hasEnded()
    }

    public get state(): ProjectState {
        if (this.isOngoing()) {
            return ProjectState.ONGOING
        }

        if (!this.hasStarted()) {
            return ProjectState.UPCOMING
        }

        if (this.hasEnded()) {
            return ProjectState.ENDED
        }
    }
}