import LocalStorageService from './LocalStorageService'

export default class TokenService {
  private static TOKEN_KEY = 'token'
  private readonly localStorageService: LocalStorageService<string>

  public constructor(localStorageService: LocalStorageService<string>) {
    this.localStorageService = localStorageService
  }

  public getToken(): string | null {
    return this.localStorageService.getItem(TokenService.TOKEN_KEY) || null
  }

  public setToken(token: string): void {
    this.localStorageService.setItem(TokenService.TOKEN_KEY, token)
  }

  public clearToken(): void {
    this.localStorageService.removeItem(TokenService.TOKEN_KEY)
  }
}