import TokenService from './TokenService'
import HttpClient from './HttpClient'
import UserService from './UserService'
import User from '../domain/User'

export default class AccountService {
  private readonly httpClient: HttpClient
  private readonly tokenService: TokenService
  private readonly userService: UserService

  public constructor(httpClient: HttpClient, tokenService: TokenService, userService: UserService) {
    this.httpClient = httpClient
    this.tokenService = tokenService
    this.userService = userService
  }

  public async login(email: string, password: string): Promise<void> {
    // const { data } = await this.httpClient.post<{ token: string }>('/account/login', { email, password })
    //this.tokenService.setToken(data.token)

    const users = await this.userService.getMany({email})

    if (!users.length) {
      throw new Error('User not found')
    }

    this.tokenService.setToken(users[0].id)
  }

  public async register(email: string, password: string): Promise<void> {
    /*await this.httpClient.post('/account/register', {
      email,
      password
    })*/

    const users = await this.userService.getMany({email})

    if (users.length) {
      throw new Error('User already exists')
    }

    const user = new User()
    user.email = email

    await this.userService.create(user)
  }

  public async getAccount(): Promise<User | null> {
    const token = this.tokenService.getToken()

    if (!token) {
      return null
    }

    return this.userService.getOne(token)
  }

  public async logout(): Promise<void> {
    this.tokenService.clearToken()
  }
}