import React from 'react'
import ActiveProjects from './views/ActiveProjects'
import {CalendarIcon, SettingsIcon, ViewIcon, TimeIcon} from '@chakra-ui/icons'
import UpcomingProjects from './views/UpcomingProjects'
import EndedProjects from './views/EndedProjects'

export interface Route {
  path: string
  title: string
  icon?: React.ReactNode,
  component?: React.FC,
  children?: Route[]
}

const routes: Route[] = [
  {
    path: '/my-projects',
    title: 'My projects',
    children: [
      {
        path: '/active-projects',
        title: 'Active projects',
        component: ActiveProjects,
        icon: <ViewIcon />,
      },
      {
        path: '/upcoming-projects',
        title: 'Upcoming projects',
        component: UpcomingProjects,
        icon: <CalendarIcon />,
      },
      {
        path: '/ended-projects',
        title: 'History',
        component: EndedProjects,
        icon: <TimeIcon />,
      }
    ]
  },
  {
    path: '/profile',
    title: 'Profile',
    children: [
      {
        path: '/personal-profile',
        title: 'Personal Profile',
        component: ActiveProjects,
        icon: <SettingsIcon />
      }
    ]
  }
]

export default routes