import RootService from '../services'
import PersonStore from './PersonStore'
import AccountStore from './AccountStore'
import ProjectStore from './ProjectStore'
import OrganizationStore from './OrganizationStore'
import SkillStore from './SkillStore'
import ProjectSkillStore from './ProjectSkillStore'

export default class RootStore {
  public readonly accountStore: AccountStore
  public readonly personStore: PersonStore
  public readonly projectStore: ProjectStore
  public readonly organizationStore: OrganizationStore
  public readonly skillStore: SkillStore
  public readonly projectSkillStore: ProjectSkillStore

  public constructor(rootService: RootService) {
    this.accountStore = new AccountStore(rootService.accountService)
    this.personStore = new PersonStore(rootService, this)
    this.projectStore = new ProjectStore(rootService, this)
    this.organizationStore = new OrganizationStore(rootService, this)
    this.skillStore = new SkillStore(rootService, this)
    this.projectSkillStore = new ProjectSkillStore(rootService, this)
  }
}