import React from 'react'
import {Switch, Route} from 'react-router-dom'
import routes from './routes'
import ConnectedLayout from './components/ConnectedLayout'
import AuthenticationGuard from './components/AuthenticationGuard'
import PersonGuard from './components/PersonGuard'
import Home from './views/Home'
import Logout from './views/Logout'
import Start from './views/Start'
import OrganizationWizard from './views/OrganizationWizard'

function App() {
  return (
      <Switch>
        <Route path="/home" component={Home} />
        <Route path="/logout" component={Logout} />
        <Route path="/start" component={Start} />
        <Route path="/create-organization" component={OrganizationWizard} />
        <Route path="*">
          <AuthenticationGuard>
            <PersonGuard>
              <ConnectedLayout routes={routes} />
            </PersonGuard>
          </AuthenticationGuard>
        </Route>
      </Switch>
  )
}

export default App
