import React from 'react'
import {Badge} from '@chakra-ui/react'
import Project, {ProjectState} from '../domain/Project'

export interface ProjectBadgeProps {
  project: Project
}

const ProjectBadge: React.FC<ProjectBadgeProps> = ({project}) => {
  let colorScheme: string
  let text: string

  switch (project.state) {
    case ProjectState.ENDED:
      colorScheme = 'red'
      text = 'Closed'
      break

    case ProjectState.UPCOMING:
      colorScheme = 'yellow'
      text = 'Upcoming'
      break

    case ProjectState.ONGOING:
      colorScheme = 'green'
      text = 'Active'
      break

    default:
      return null
  }

  return (
      <Badge colorScheme={colorScheme} borderRadius="md">{text}</Badge>
  )
}

export default ProjectBadge