import axios  from 'axios'
import TokenService from './TokenService'
import HttpClient from './HttpClient'

export default class HttpClientService {
  private readonly tokenService: TokenService

  public constructor(tokenService: TokenService) {
    this.tokenService = tokenService
  }

  public create(apiURL?: string): HttpClient {
    const httpClient = axios.create({
      baseURL: apiURL
    })

    httpClient.interceptors.request.use(async (config) => {
      const token = await this.tokenService.getToken()

      if (token) {
        config.headers.authorization = `Bearer ${token}`
      }

      if (!config.headers.Accept) {
        config.headers.Accept = 'application/json'
      }

      return config
    })

    httpClient.interceptors.response.use(undefined, error => {
      const { title, detail, message } = error?.response?.data || {}
      error.message = title || detail || message || error.message

      return Promise.reject(error)
    })

    return httpClient
  }
}