import AbstractRestfulLocalStorageService from './AbstractRestfulLocalStorageService'
import Person from '../domain/Person'
import LocalStorageService from './LocalStorageService'
import PersonService from './PersonService'
import TokenService from './TokenService'

export default class LocalPersonService extends AbstractRestfulLocalStorageService<Person> implements PersonService {
  private readonly tokenService: TokenService

  public constructor(
      localStorageService: LocalStorageService<Person[]>,
      tokenService: TokenService
  ) {
    super(Person, localStorageService)
    this.tokenService = tokenService
  }

  public async getAllRelatedToUser(): Promise<Person[]> {
    return this.getMany({ userId: this.tokenService.getToken() })
  }

  public async getAllByOrganization(organizationId: string): Promise<Person[]> {
    return this.getMany({ organizationId })
  }
}