import React from 'react'
import {Avatar, HStack, Text, StackProps} from '@chakra-ui/react'

export interface UserProps extends StackProps {
  name: string,
  subTitle: string,
  image?: string
}

const User: React.FC<UserProps> = ({ name, subTitle, image, ...stackProps}) => {
  return (
      <HStack spacing={2} {...stackProps}>
        <Text align="right">
          <Text fontSize="sm">{name}</Text>
          <Text fontSize="xs" color="gray.500">{subTitle}</Text>
        </Text>
        <Avatar name={name} src={image} size="sm" />
      </HStack>
  )
}

export default User