import React, {useEffect} from 'react'
import {Container} from '@chakra-ui/react'
import OrganizationForm from '../components/OrganizationForm'
import useStore from '../hooks/useStore'
import {useHistory} from 'react-router-dom'

const OrganizationWizard: React.FC = () => {
  const {organizationStore, personStore, accountStore} = useStore()
  const history = useHistory()

  useEffect(() => {
    accountStore.checkAuthentication()
  }, [])

  const onSubmit = async ({person, organization}: any) => {
    const savedOrganization = await organizationStore.create(organization)
    const savedPerson = await personStore.create({
      ...person,
      organizationId: savedOrganization.id,
      userId: accountStore.authenticatedUser.id
    })
    personStore.selectPerson(savedPerson.id)
    history.push('/')
  }

  return (
      <Container>
        <OrganizationForm onSubmit={onSubmit}/>
      </Container>
  )
}

export default OrganizationWizard