import RootService from '../services'
import RootStore from './index'
import AbstractResourceStore from './AbstractResourceStore'
import Organization from '../domain/Organization'
import {computed} from 'mobx'

export default class OrganizationStore extends AbstractResourceStore<Organization> {
  private readonly rootStore: RootStore

  public constructor(rootService: RootService, rootStore: RootStore) {
    super(rootService.organizationService, rootStore.accountStore)
    this.rootStore = rootStore
  }

  @computed
  public get currentOrganizationId(): string | null {
    return this.rootStore.personStore.currentPerson?.organizationId
  }

  @computed
  public get currentOrganization() {
    return this.currentOrganizationId && this.getByIdOrLoad(this.currentOrganizationId)
  }
}