import AbstractDomain from './AbstractDomain'
import Project from './Project'
import Skill from './Skill'
import {JsonProperty} from 'jackson-js'
import Resource from '../decorators/Resource'

@Resource.Name('projectSkills')
export default class ProjectSkill extends AbstractDomain<ProjectSkill> {
    @JsonProperty() public skillId: string
    @JsonProperty() public projectId: string
    @JsonProperty() public capacity: number
    protected _project?: Project
    protected _skill?: Skill

    @JsonProperty()
    get skill(): Skill {
        return this._skill
    }

    set skill(value: Skill) {
        this._skill = value
    }

    @JsonProperty()
    get project(): Project {
        return this._project
    }

    set project(value: Project) {
        this._project = value
    }
}